export default {
    data() {
        return {
            menuopen: false
        }
    },
    methods: {
        openlink(link) {
            window.open(link, '_blank');
        },
        togglemenu() {
            this.menuopen ? this.menuopen = false : this.menuopen = true;
        },
        moveToRoute(route) {
            this.$router.push(route)
        }
    }
}
